@import url(https://fonts.googleapis.com/css?family=Roboto+Montserrat);
@import url(https://fonts.googleapis.com/css?family=Roboto+Lato+Montserrat);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: #f4f4f4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html{
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #333;
  background-size: cover;
  background: rgb(43,50,81);
  background: -webkit-gradient(linear, left top, right top, from(rgba(14,14,34,1)), color-stop(50%, rgba(21,21,46,1)), to(rgba(14,14,34,1)));
  background: linear-gradient(90deg, rgba(14,14,34,1) 0%, rgba(21,21,46,1) 50%, rgba(14,14,34,1) 100%);
}
.App {
  text-align: center;
}


.topheader {
  background-color: #174c80;
}

.navbar {
  background-color: #174c80;
}

.navbar-item {
  color: #fff;
}

.results--section {
  padding: 10px 15px;
  margin-top: 10px;
}


h1 {
  text-align: center;
  font-size: 12px;
  letter-spacing: 1.52em;
  margin-left: 25px;
}

.today--section {
  padding: 30px;
}

.today--section h2 {
  font-size: 20px;
}

.today--section__box {
  background-color: rgb(24,28,44);
  padding: 10px;
  border-radius: 14px;
  box-shadow:  5px 5px 20px 0 rgba(2, 2, 2, 0.5);
}

.symbol--icon{
  width: 20%;
  display: block;
  position: absolute;

}

.btc--section {
  padding-left: 30%;
  border-right: 1px solid #dae1e9;
}

.btc--section h5 {
  font-size: 30px;
  font-family: "Roboto Bold";
  font-weight: 600;
}

.eth--section {
  text-align: center;
  border-right: 1px solid #dae1e9;
}

.eth--section h5 {
  font-size: 30px;
}
.ltc--section {
  text-align: center;
  border-right: 1px solid #dae1e9;
}

.ltc--section h5 {
  font-size: 30px;
}

@media (max-width: 480px) {
  .eth--section {
    border-right: none;
  }

  .btc--section {
    border-right: none;
  }

  .today--section {
    margin-top: 5px;
  }
}

